import * as React from 'react'
import { Link } from 'gatsby';
import { PaperAirplaneIcon, TicketIcon } from '@heroicons/react/24/outline'

const Button = ({ label, icon, to, color }) => {
  const buttonStyle = {
    blue: 'text-white font-bold py-2 px-4 rounded mr-2 md:mr-4 submit-talk-button',
    green: 'text-white font-bold py-2 px-4 rounded mr-2 md:mr-4 buy-tickets-button'
  }

  // Choose the icon component based on the 'icon' prop
  const IconComponent = icon === 'submit' ? PaperAirplaneIcon : TicketIcon;

  return (
    <Link to={to} className={`${buttonStyle[color]}`}>
        {/* Show icon on smaller screens */}
        <IconComponent className="h-5 w-5 buttons-breakpoint:hidden" aria-hidden="true" />
        {/* Show label on larger screens */}
        <span className="hidden buttons-breakpoint:block">{label}</span>
    </Link>
  );
};

export default Button;
